import { Component, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { of } from 'rxjs';
import { TransactionsCheckImage } from '../../model/transactions-check-images.model';
import { DocumentRefSrc, getKeyCode, KEY_CODES } from '../../model/UI/document-viewer-commons';
type OptionalCheckImage = TransactionsCheckImage | undefined;

@Component({
  selector: 'bb-transaction-check-image-document-viewer',
  templateUrl: 'transaction-check-image-document-viewer.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionCheckImageDocumentViewerComponent {
  @Input() imageCheckNumber: string | undefined | number;
  @Input() imageFront: TransactionsCheckImage | undefined;
  @Input() imageBack: TransactionsCheckImage | undefined;
  @Input() position = 0;

  @Output() closeViewer = new EventEmitter<void>();

  private toDocumentRefSrc(...images: OptionalCheckImage[]): DocumentRefSrc[] {
    return images
      .filter((item) => !!item)
      .map((image) => {
        const { mimeType, content, side } = image as TransactionsCheckImage;
        const fileType = image?.mimeType.replace('image/', '');
        const filename = `${this.imageCheckNumber}-${side}.${fileType}`;

        return {
          filename,
          filetype: image?.mimeType as any,
          src: of(`data:${mimeType};base64,${content}`),
        };
      });
  }

  get imagesRefsSrc() {
    return this.toDocumentRefSrc(this.imageFront, this.imageBack);
  }

  closeDocumentViewer() {
    this.closeViewer.emit();
  }

  trapKeyEvent(event: KeyboardEvent) {
    if (getKeyCode(event) !== KEY_CODES.ESCAPE) {
      event.stopPropagation();
    }
  }
}
